import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			address: '',
			imgList: [],
			temp_address: '',
			storeInfo: {
				wei: null,
				name: null,
				address: null,
				phone: null,
				image: null,
				latitude: null,
				longitude: null
			},
		}
	},
	computed: {
		...mapState(['globalOpenid', 'globalWXuserInfo'])
	},
	onLoad() {
		var that = this;
		that.initData();
	},
	methods: {
		...mapMutations(['setGlobalUserInfo', 'setGlobalOpenid', 'setglobalWXuserInfo', 'setToken']),
		// 数据提交
		signUpNow() {
			var that = this;
			that.userBindInfo();
		},
		// 商户绑定信息
		userBindInfo() {
			var that = this;
			var tempData = that.storeInfo;
			if (tempData.image == null) {
				uni.showToast({
					title: '图片未上传！',
					icon: 'none'
				})
			} else if (tempData.address == null) {
				uni.showToast({
					title: '地址为空！',
					icon: 'none'
				})
			} else if (tempData.name == null) {
				uni.showToast({
					title: '商户名为空！',
					icon: 'none'
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '确定信息正确？',
					success: function(res) {
						if (res.confirm) {
							uni.showLoading({
								title: '提交中'
							})
							uni.request({
								url: that.global_baseUrl + '/index.php/shopSigin',
								data: tempData,
								method: "POST",
								success: (res) => {
									if (res.data.code == '200') {
										uni.setStorageSync('localUserInfo', res.data.data);
										that.setToken(res.data.data.token);
										uni.setStorageSync('token', res.data.data.token)
										that.setGlobalUserInfo(res.data.data)
										uni.setStorageSync('isDone', 0)
										uni.hideLoading();
										uni.showToast({
											title: '等待审核'
										})
										setTimeout(() => {
											uni.hideLoading();
											uni.redirectTo({
												url: '/pages/login/login?isDone=0'
											})
										}, 800)
									} else if (res.data.code == 501) {
										uni.showToast({
											title: "名字长度不能超过4个字",
											icon: 'none'
										});
										uni.hideLoading();
									} else if (res.data.code == 401) {
										uni.showToast({
											title: "号码格式不正确!",
											icon: 'none'
										});
										uni.hideLoading();
									} else if (res.data.code == 504) {
										uni.showModal({
											title: '提示',
											content: '商户已绑定',
											success: function(res) {
												if (res.confirm) {
													console.log('用户点击确定');
												} else if (res.cancel) {
													console.log('用户点击取消');
												}
											}
										});
										uni.hideLoading();
									}
								}
							});
						}
					}
				});
			}
		},
		initData() {
			var that = this;
			that.storeInfo.wei = that.globalOpenid;
		},
		// 商户信息表单
		inputMehtod(e) {
			var that = this;
			switch (e.target.dataset.key) {
				case 'name':
					that.storeInfo.name = e.target.value
					break;
				case 'phone':
					that.storeInfo.phone = e.target.value
					break;
				default:
					break;
			}
		},
		//地图选择地址
		chooseLocation() {
			var _self = this;
			uni.showLoading({
				title: '加载地图模块中...'
			})
			uni.chooseLocation({
				success: (data) => {
					_self.storeInfo.latitude = data.latitude;
					_self.storeInfo.longitude = data.longitude;
					_self.storeInfo.address = data.address;
					_self.temp_address = ''
					_self.temp_address = data.address

				}
			})
			setTimeout(() => {
				uni.hideLoading();
			}, 500)
		},
		setValue(do_what, value) {
			switch (do_what) {
				case 'name':
					this.addressData.name = value;
					break;
				case 'mobile':
					this.addressData.mobile = value;
					break;
				case 'area':
					this.addressData.area = value;
					break;
				case 'address':
					this.addressData.address = value;
			}
		},
		// 图片上传测试
		userUploadFile(picPath, belongUser, picType) {
			var that = this;
			var tempData = {
				belongUser: belongUser,
				picType: picType
			}
			uni.uploadFile({
				url: that.global_baseUrl + '/index.php/upPic',
				filePath: picPath,
				name: 'file',
				header: {
					'content-type': 'application/json'
				},
				formData: tempData,
				success: (uploadFileRes) => {
					console.log(uploadFileRes)
					const tempPath = JSON.parse(uploadFileRes.data).data.src;
					that.storeInfo.image = tempPath.replace(/\\/g, "/");
				}
			});
		},
		ChooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					this.userUploadFile(res.tempFilePaths[0], 1, 1)
					if (this.imgList.length != 0) {

						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},

		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '提示',
				content: '你真的忍心删掉我吗',
				cancelText: '不',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
	}
}
